.container {
    position: relative;
}
.container.isLocal {
    display: inline-block;
    position: absolute;
    top: 52px;
    right: 16px;
}
.name {
    position: absolute;
    bottom: 0px;
    left: 0px;
    font-size: 16px;
    color: white;
    display: block;
    width: 100%;
    margin: 0px;
    padding: 8px;
    background-color: rgb(0, 0, 0, 0.3);
}

.video {
    width: 90%;
    object-fit: cover;
    border-radius: 48px;
    border: 2px solid white;
}
